<template>
  <div>
    <div class="container scontainer" >
      <div class="search-bar">
        <van-search
          v-model="keywords"
          show-action
          label="查询"
          placeholder="请输入搜索关键词"
          @search="searchData"
          clearable
        >
          <template #action>
            <div @click="searchData">搜索</div>
          </template>
        </van-search>
      </div>
     
      <Empty ></Empty>

    </div>
  </div>
</template>

<script>
import { getMonthOrder } from "@api/public";
import { Icon, Search, Loading, List } from "vant";
import { formatDate } from "@utils/validate";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
export default {
  name: "CustList",
  components: {
    [Icon.name]: Icon,
    [Search.name]: Search,
    [Loading.name]: Loading,
    [List.name]: List,
    Empty,
    Loadings
  },

  data: function () {
    return {
      list: null,
      keywords: "",
      month: "",
      page: "",
      loading: false,
      finished: false,
    };
  },
  created() {
    
  },
  mounted: function () {
    this.month = this.$route.params.id;

  },
  methods: {
    searchData() {
      if (this.keywords != "") {
        this.page = "";
      }
      this.getData();
    },
    toThousands(num) {
      var result = [],
        counter = 0;
      num = (num || 0).toString().split("");
      for (var i = num.length - 1; i >= 0; i--) {
        counter++;
        result.unshift(num[i]);
        if (!(counter % 3) && i != 0) {
          result.unshift(",");
        }
      }
      return result.join("");
    },
    getData() {
      var data = {
        search: this.keywords,
        page: this.page,
      };
      if (this.month != -1) {
        data.month = this.month;
      }
      getMonthOrder(data).then((res) => {
        res.data.order.forEach((item) => {
          item.cr_time = formatDate(item.cr_time);
        });

        if (this.page != "") {
          this.list = this.list.concat(res.data.order);
        } else {
          this.list = res.data.order;
        }

        this.finished = res.data.order.length < 200 ? true : false;
        this.loading = res.data.order.length < 200 ? true : false;
        this.page = this.list[this.list.length - 1].oid;
      });
    },
    onLoad() {
      this.getData();
    },
  },
};
</script>
<style scoped>
.container {
  background: #fff;
}

.container {
  background-color: #fff;

  border-radius: 5px;
}

.container .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #333;
}

.caption .month {
  height: 25px;
  width: 60px;
  background: #092e48;
  border-radius: 20px;
  color: #fff;
  line-height: 25px;
  font-size: 12px;
}

.caption .month span {
  display: inline-block;
  width: 23px;
  height: 23px;
  background: #fff;
  border-radius: 50%;
  margin-left: 1px;
  margin-right: 4px;
}

.container .list {
  line-height: 43px;
  font-size: 14px;
}

.container .list .item {
  padding: 15px 8px;
  display: block;
  border-bottom: 10px solid #f9f9f9;
}

.container .list .item div {
  width: 100%;
}

.container .list img {
  width: 20px;
  margin-right: 10px;
}

.container .list .info {
  line-height: 18px;
  font-size: 12px;
}

.container .list .item .right {
  padding-right: 15px;
  font-weight: bold;
}

.container .list .item .right span {
  font-size: 10px;
  font-weight: 500;
}

.company {
  color: #333;
  font-size: 16px;
}

.contact {
  color: #999;
  margin: 5px 0;
  font-size: 12px;
}

.contact span {
  padding-left: 10px;
}

.address {
  color: #999;
  font-size: 12px;
  margin-bottom: 5px;
}

.list img {
  width: 30px;
}

[v-cloak] {
  display: none;
}

.loading-tips {
  text-align: center;
  padding-top: 150px;
}

.consumption {
  font-size: 12px;
  color: #999;
}
</style>
